//引入图标组件
import logobg from "@/components/login/loginbg.vue"
export default {
	data() {//这是短信登录
		return {
			countdowntext:"验证码",
			wait:60,
			disabled:true,
			bool:false
		}
	},
	methods: {
		//点击验证码时
		countdown(){
			this.disabled = false
			//这是当前的时候
			// let date = new Date()
			// //这是1分钟后的时间
			// let latetime = new Date()
			this.time()
		},
		time(){
			let times = null
			this.bool = true
			//这块点击反复执行定时器
			// clearInterval(times)
			let {countdowntext,wait} = this.$data
			// console.log(countdowntext,wait)
				times = setInterval(()=>{
					wait--
					// console.log(wait)
					this.countdowntext = wait
					if(wait==0){
						this.bool = false
						countdowntext = "重新获取验证码"
						clearInterval(times)
						this.countdowntext = countdowntext
						this.wait = 60
					}
					
				},1000)
		},
		//点击button的时候
		smslogin(e){
			console.log("点击按钮生效")
			console.log(e)
		},
		registration(){
			uni.navigateTo({
				url:"/pages/Freeregistration/Freeregistration"
			})
		}
	},
	components:{
		logobg
	}
}
